<template>
  <div class="ReservationService">
    <div
      class="governmentService"
      :class="status ? 'governmentServiceactive' : ''"
      @click="statusClick(true)"
    >
      到府服務
    </div>
    <div
      class="governmentService"
      :class="!status ? 'governmentServiceactive' : ''"
      @click="statusClick(false)"
    >
      到店取貨
    </div>
    <!-- 到府服务 -->
    <div v-if="status">
      <div
        v-for="(item, index) in governmentServiceListA"
        :key="index"
        class="ReservationServiceGovernmentService"
      >
        <div class="GovernmentServiceTop" @click="clickStatus(false, index)">
          <img
            v-if="item.CustPictureUrl"
            draggable="false"
            :src="item.CustPictureUrl"
            alt=""
          />
          <img
            v-if="!item.CustPictureUrl"
            draggable="false"
            src="@/assets/image/InstallHeadPortrait.png"
            alt=""
          />
          <div>
            <h2>
              {{ item.CustName }}
              <em v-if="item.replyStatus">{{ item.replyStatus }}</em>
            </h2>
            <p>
              型號：{{ item.ModelType }} 更換濾心數：{{
                item.OrderDetailList.length
              }}
            </p>
            <p>申請日期：{{ item.DisplayCreateOn }}</p>
          </div>
          <!-- <span>
            <img
              v-if="statusList[index]"
              @click="clickStatus(false, index)"
              class="imgBottom"
              draggable="false"
              src="@/assets/image/bottom.png"
              alt=""
            />
            <img
              v-if="!statusList[index]"
              @click="clickStatus(true, index)"
              class="imgRight"
              draggable="false"
              src="@/assets/image/right.png"
              alt=""
            />
          </span> -->
        </div>
        <div v-if="item.BookingDate" class="GovernmentServiceTime">
          到府服務時間：{{ item.tiem }}
        </div>
        <div v-if="statusList[index]" class="GovernmentServiceCenter">
          <div
            v-for="(items, index) in item.OrderDetailList"
            :key="index"
            class="GovernmentServiceCenter_box"
          >
            <div>
              <img
                draggable="false"
                src="@/assets/image/GovernmentServiceCenter.png"
                alt=""
              />
              <h3>
                <p>{{ items.FilterDescription }}</p>
                <span>{{ items.FilterName }}</span>
              </h3>
            </div>
            <h4>數量：{{ items.Quantity }}</h4>
          </div>
        </div>
        <div v-if="statusList[index]" class="GovernmentServiceBottom">
          <div @click="toCustomerProfile(item.CustId, item.CustMobile)">
            客戶資料
          </div>
          <span v-if="item.ServiceStatus == 'B005'">結案</span>
          <div
            v-if="item.ServiceBtnText == '受理' && item.ServiceStatus != 'B005'"
            @click="
              affirmShowPopup(item.OrderId, item.Reason, item.ServiceStatus)
            "
          >
            受理
          </div>
          <div
            v-if="item.ServiceBtnText != '受理' && item.ServiceStatus != 'B005'"
            @click="
              affirmShowPopup(item.OrderId, item.Reason, item.ServiceStatus)
            "
          >
            修改
          </div>
        </div>
      </div>
    </div>
    <!-- 到府服务 -->
    <!-- 到店取货 -->
    <div v-if="!status" class="ReservationServiceStorePickup">
      <div
        v-for="(item, index) in governmentServiceListB"
        :key="index"
        class="ReservationServiceGovernmentService"
      >
        <div class="GovernmentServiceTop" @click="clickStatuss(false, index)">
          <img
            v-if="item.CustPictureUrl"
            draggable="false"
            :src="item.CustPictureUrl"
            alt=""
          />
          <img
            v-if="!item.CustPictureUrl"
            draggable="false"
            src="@/assets/image/InstallHeadPortrait.png"
            alt=""
          />
          <div>
            <h2>
              {{ item.CustName }}
              <em v-if="item.replyStatus">{{ item.replyStatus }}</em>
            </h2>
            <p>
              型號：{{ item.ModelType }} 更換濾心數：{{
                item.OrderDetailList.length
              }}
            </p>
            <p>申請日期：{{ item.DisplayCreateOn }}</p>
          </div>
          <!-- <span>
            <img
              v-if="statusLists[index]"
              @click="clickStatuss(false, index)"
              class="imgBottom"
              draggable="false"
              src="@/assets/image/bottom.png"
              alt=""
            />
            <img
              v-if="!statusLists[index]"
              @click="clickStatuss(true, index)"
              class="imgRight"
              draggable="false"
              src="@/assets/image/right.png"
              alt=""
            />
          </span> -->
        </div>
        <div v-if="item.BookingDate" class="GovernmentServiceTime">
          到店取貨時間：{{ item.tiem }}
        </div>
        <div v-if="statusLists[index]" class="GovernmentServiceCenter">
          <div
            v-for="(items, index) in item.OrderDetailList"
            :key="index"
            class="GovernmentServiceCenter_box"
          >
            <div>
              <img
                draggable="false"
                src="@/assets/image/GovernmentServiceCenter.png"
                alt=""
              />
              <h3>
                <p>{{ items.FilterDescription }}</p>
                <span>{{ items.FilterName }}</span>
              </h3>
            </div>
            <h4>數量：{{ items.Quantity }}</h4>
          </div>
        </div>
        <div v-if="statusLists[index]" class="GovernmentServiceBottom">
          <div @click="toCustomerProfile(item.CustId, item.CustMobile)">
            客戶資料
          </div>
          <span v-if="item.ServiceStatus == 'B005'">結案</span>
          <div
            v-if="item.ServiceBtnText == '受理' && item.ServiceStatus != 'B005'"
            @click="
              stockUpshowPopup(item.OrderId, item.Reason, item.ServiceStatus)
            "
          >
            受理
          </div>
          <div
            v-if="item.ServiceBtnText != '受理' && item.ServiceStatus != 'B005'"
            @click="
              stockUpshowPopup(item.OrderId, item.Reason, item.ServiceStatus)
            "
          >
            修改
          </div>
        </div>
      </div>
    </div>
    <!-- 到店取货 -->
    <!-- 完成弹窗 -->
    <van-popup v-model="show" class="ReservationServicePopupSuccess">
      <img draggable="false" src="@/assets/image/success.png" alt="" />
      <h2>完成</h2>
      <div @click="hidePopup()">返回預約服務列表</div>
    </van-popup>
    <!-- 到府服务受理弹窗 -->
    <van-popup v-model="affirmShow" class="ReservationServicePopupAffirm">
      <h2>請選擇</h2>
      <van-radio-group v-model="radioAffirm">
        <van-radio name="B003">已聯繫完成</van-radio>
        <van-radio name="B005">已完成結案</van-radio>
        <van-radio name="B008">其他原因</van-radio>
      </van-radio-group>
      <textarea
        style="resize:none;"
        v-model="reasonCancellation"
        placeholder="請填寫原因"
      ></textarea>
      <div class="ReservationServicePopupAffirmBottom">
        <span @click="affirmShow = false">取消</span>
        <em @click="affirmHidePopup()">送出</em>
      </div>
    </van-popup>
    <!-- 到店取货受理弹窗 -->
    <van-popup v-model="StockUpShow" class="ReservationServicePopupStockUp">
      <h2>請選擇</h2>
      <van-radio-group v-model="radioStockUp">
        <van-radio name="B004">已備貨完成</van-radio>
        <van-radio name="B005">已完成結案</van-radio>
        <van-radio name="B008">其他原因</van-radio>
      </van-radio-group>
      <textarea
        style="resize:none;"
        v-model="StockUpReasonCancellation"
        placeholder="請填寫原因"
      ></textarea>
      <div class="ReservationServicePopupStockUpBottom">
        <span @click="StockUpShow = false">取消</span>
        <em @click="stockUpHidePopup()">送出</em>
      </div>
    </van-popup>
    <!-- 空组件 -->
    <noContent v-if="dataFormListStatus && status" />
    <noContent v-if="dataFormListStatuss && !status" />
    <!-- 加载中组件 -->
    <Loading :show="loadingShow" />
  </div>
</template>

<script>
import {
  POST_GetOrderHistoryByDistribution,
  POST_UpdateOrderStatus
} from "@/utils/api";
import noContent from "@/components/noContent";
import Loading from "@/components/Loading";
export default {
  name: "ReservationService",
  inject: ["reload"],
  components: { noContent, Loading },
  data() {
    return {
      loadingShow: false,
      dataFormListStatus: false,
      dataFormListStatuss: false,
      orderId: "",
      statusList: [],
      statusLists: [],
      // 完成弹窗
      show: false,
      // 原因弹窗
      affirmShow: false,
      radioAffirm: "B003",
      reasonCancellation: "",
      // 备货弹窗
      StockUpShow: false,
      StockUpReasonCancellation: "",
      radioStockUp: "B004",
      status: true,
      imgStatus: false,
      governmentServiceListA: [],
      governmentServiceListB: [],
      governmentServiceList: []
    };
  },
  created() {
    this.GetOrderHistoryByDistribution();
  },
  methods: {
    // 跳轉到客戶資料
    toCustomerProfile(id, CustPhone) {
      this.$router.push(
        "/CustomerService/CustomerData/CustomerDataDetails?id=" +
          id +
          "&CustPhone=" +
          CustPhone
      );
    },
    // 获取到府服务列表
    GetOrderHistoryByDistribution() {
      this.loadingShow = true;
      var UofIds = {
        UOFid: sessionStorage.getItem("DistributionUofId")
      };
      this.governmentServiceList = [];
      this.governmentServiceListA = [];
      this.governmentServiceListB = [];
      this.statusList = [];
      this.statusLists = [];
      POST_GetOrderHistoryByDistribution(UofIds).then(response => {
        this.loadingShow = false;
        if (response.State) {
          if (this.$route.query.CustPhone) {
            // 循环取值
            for (var i = 0; i < response.Data.length; i++) {
              // 设置年月日，拆分重组成显示的格式
              this.governmentServiceList.push(response.Data[i]);
              var time1 = this.governmentServiceList[d].DisplayCreateOn.split(
                " "
              )[0];
              var time2 = time1.split("-");
              this.governmentServiceList[d].DisplayCreateOn =
                time2[0] + "年" + time2[1] + "月" + time2[2] + "日";
              var a = this.governmentServiceList[i].BookingDate.split("T")[0];
              var s = a.split("-");
              var t = this.governmentServiceList[i].BookingTime
                ? this.governmentServiceList[i].BookingTime
                : "00:00";
              // this.governmentServiceList[i].tiem =
              //   s[0] + "年" + s[1] + "月" + s[2] + "日" + " " + t;
              this.governmentServiceList[i].tiem =
                s[0] + "年" + s[1] + "月" + s[2] + "日";
              // 判断当前下标的对象的号码等于搜索号码
              if (response.Data[i].CustMobile == this.$route.query.CustPhone) {
                // 判断类型为專人到府
                if (this.governmentServiceList[i].BookingType == "專人到府") {
                  // 将类型为專人到府的对象添加进專人到府列表数组
                  this.governmentServiceListA.push(
                    this.governmentServiceList[i]
                  );
                  // 对应的点击展示值也添加进去
                  this.statusList.push(false);
                  // 电话号码对上了,隐藏对应的空组件
                  this.dataFormListStatus = false;
                  // 判断类型为取貨付款
                } else if (
                  this.governmentServiceList[i].BookingType == "取貨付款"
                ) {
                  // 将类型为取貨付款的对象添加进取貨付款列表数组
                  this.governmentServiceListB.push(
                    this.governmentServiceList[i]
                  );
                  // 对应的点击展示值也添加进去
                  this.statusLists.push(false);
                  // 电话号码对上了,隐藏对应的空组件
                  this.dataFormListStatuss = false;
                }
              }
            }
          } else {
            // 正常赋值
            for (var d = 0; d < response.Data.length; d++) {
              // 申请日期拆分组装
              this.governmentServiceList.push(response.Data[d]);
              var time1 = this.governmentServiceList[d].DisplayCreateOn.split(
                " "
              )[0];
              var time2 = time1.split("-");
              this.governmentServiceList[d].DisplayCreateOn =
                time2[0] + "年" + time2[1] + "月" + time2[2] + "日";
              var aa = this.governmentServiceList[d].BookingDate.split("T")[0];
              var ss = aa.split("-");
              var tt = this.governmentServiceList[d].BookingTime
                ? this.governmentServiceList[d].BookingTime
                : "00:00";
              // this.governmentServiceList[d].tiem =
              //   ss[0] + "年" + ss[1] + "月" + ss[2] + "日" + " " + tt;
              this.governmentServiceList[d].tiem =
                ss[0] + "年" + ss[1] + "月" + ss[2] + "日";

              if (this.governmentServiceList[d].BookingType == "專人到府") {
                this.governmentServiceListA.push(this.governmentServiceList[d]);
                this.statusList.push(false);
              } else if (
                this.governmentServiceList[d].BookingType == "取貨付款"
              ) {
                this.governmentServiceListB.push(this.governmentServiceList[d]);
                this.statusLists.push(false);
              }
            }
          }
          // 判断到府服务list长度，为0则显示空组件，反之则隐藏
          if (this.governmentServiceListA.length) {
            this.dataFormListStatus = false;
          } else {
            this.dataFormListStatus = true;
          }
          // 判断到店取货list长度，为0则显示空组件，反之则隐藏
          if (this.governmentServiceListB.length) {
            this.dataFormListStatuss = false;
          } else {
            this.dataFormListStatuss = true;
          }
        } else {
          // 请求失败也显示空组件
          this.dataFormListStatus = true;
          this.dataFormListStatuss = true;
        }
      });
    },
    // 到付服务点击展开
    clickStatus(item, index) {
      var status = !this.statusList[index];
      this.statusList.splice(index, 1, status);
    },
    // 到店取货点击展开
    clickStatuss(item, index) {
      var status = !this.statusLists[index];
      this.statusLists.splice(index, 1, status);
      // this.statusLists.splice(index, 1, item);
    },
    // 关闭完成弹窗
    hidePopup() {
      this.show = false;
      this.GetOrderHistoryByDistribution();
      // this.reload();
      // this.$router.push("/CustomerService");
    },
    // 打开到店取货受理弹窗
    affirmShowPopup(orderId, Reason, ServiceStatus) {
      this.reasonCancellation = Reason;
      this.orderId = orderId;
      this.affirmShow = true;
      if (ServiceStatus == "B003") {
        this.radioAffirm = "B003";
      } else if (ServiceStatus == "B005") {
        this.radioAffirm = "B005";
      } else if (ServiceStatus == "B008") {
        this.radioAffirm = "B008";
      } else {
        this.radioAffirm = "B003";
      }
    },
    // 关闭到店取货弹窗
    affirmHidePopup() {
      var UofIds = {
        OrderId: this.orderId,
        ServiceStatus: this.radioAffirm,
        Reason: this.reasonCancellation
      };
      POST_UpdateOrderStatus(UofIds).then(response => {
        if (response.State == true) {
          this.affirmShow = false;
          this.show = true;
        } else {
          this.$notify({ type: "warning", message: response.Msg });
          this.affirmShow = false;
          this.GetOrderHistoryByDistribution();
        }
      });
    },
    // 打开到店取货受理弹窗
    stockUpshowPopup(orderId, Reason, ServiceStatus) {
      this.StockUpReasonCancellation = Reason;
      this.orderId = orderId;
      this.StockUpShow = true;
      if (ServiceStatus == "B004") {
        this.radioStockUp = "B004";
      } else if (ServiceStatus == "B005") {
        this.radioStockUp = "B005";
      } else if (ServiceStatus == "B008") {
        this.radioStockUp = "B008";
      } else {
        this.radioStockUp = "B004";
      }
    },
    // 关闭到府服务受理弹窗
    stockUpHidePopup() {
      var UofIds = {
        OrderId: this.orderId,
        ServiceStatus: this.radioStockUp,
        Reason: this.StockUpReasonCancellation
      };
      POST_UpdateOrderStatus(UofIds).then(response => {
        if (response.State == true) {
          this.StockUpShow = false;
          this.show = true;
        } else {
          this.$notify({ type: "warning", message: response.Msg });
          this.StockUpShow = false;
          this.GetOrderHistoryByDistribution();
        }
      });
    },
    statusClick(item) {
      this.status = item;
    }
  }
};
</script>

